<template>
  <page
    @on-ready="onReady"
    @on-show="onShow"
    ref="page"
    class="orderdetail-page common-page"
    :headerHeightVW="headerHeightVW"
    scrollbar
  >
    <van-nav-bar slot="header" id="header" title="工单详情" left-arrow @click-left="onClickLeft">
      <img slot="right"  @click="handleReferences" src="../../assets/images/references.png" alt="icon" class="action-menu" >
    </van-nav-bar>

    <detail v-if="detail.id" :data="detail" @show-suggestion="onShowSuggestion" @show-daiagnosis="onShowDiagnosis"></detail>

    <div class="blank-empty"></div>

    <template slot="blank">
      <div class="footer-action" v-if="detail.status == 'assigned'">
        <div class="action-item" @click="handleHang" v-if="user.position_id == 'monitor'">
          <i class="action-item-icon icon-guaqi"></i>
          <span>挂起</span>
        </div>
        <van-button @click.native="handleReceive" class="action-item-main" color="#108EE9">接报</van-button>
      </div>
      <div class="footer-action" v-if="detail.status == 'pending_sign'">
        <div class="action-item" @click="handleHang" v-if="user.position_id == 'monitor'">
          <i class="action-item-icon icon-guaqi"></i>
          <span>挂起</span>
        </div>
        <div class="action-item" @click="handleForward">
          <i class="action-item-icon icon-zhuanfa"></i>
          <span>转发</span>
        </div>
        <van-button @click.native="handleSign" class="action-item-main" color="#108EE9" :disabled="!detail.currentUser && !detail.currentUser.id && user.id != detail.currentUser.id">签到</van-button>
      </div>
      <div class="footer-action" v-if="detail.status == 'waiting_repair'">
        <div class="action-item" @click="handleHang" v-if="user.position_id == 'monitor'">
          <i class="action-item-icon icon-guaqi"></i>
          <span>挂起</span>
        </div>
        <div class="action-item" @click="handleClearup">
          <i class="action-item-icon icon-xiaoxiang"></i>
          <span>销项</span>
        </div>
        <div class="action-item" @click="handleForward">
          <i class="action-item-icon icon-zhuanfa"></i>
          <span>转发</span>
        </div>
        <van-button @click.native="handleRepair" class="action-item-main" color="#108EE9" :disabled="!detail.currentUser && !detail.currentUser.id && user.id != detail.currentUser.id">维修</van-button>
      </div>


      <van-popup
        v-model="suggestionShow"
        position="bottom"
        class="filter-popup"
        :lock-scroll="false"
        closeable
        close-icon="close"
        round
      >
        <div class="title">维修建议</div>
        <a-scroll ref="filterScroller" v-if="suggestionShow" class="filter-scroll">
          <div style="padding: 16px;">
            {{detail.repairSuggestion}}
          </div>
        </a-scroll>
        <div class="footer-action">
          <van-button class="action-item-main" color="#108EE9" @click="suggestionShow = false">关闭</van-button>
        </div>
      </van-popup>

      <van-popup
        v-model="diagnosisShow"
        position="bottom"
        class="filter-popup"
        :lock-scroll="false"
        closeable
        close-icon="close"
        round
      >
        <div class="title">专家诊断</div>
        <a-scroll ref="filterScroller" v-if="diagnosisShow" class="filter-scroll">
          <van-cell-group v-for="(item, index) in diagnosisData" :key="index">
            <van-cell title="故障原因：" class="detail-title" />
            <van-cell>
              <span style="color: #969799;">{{item.failure_reason}}</span>
            </van-cell>
            <van-cell title="分析：" class="detail-title" />
            <van-cell>
              <span style="color: #969799;">{{item.failure_parse}}</span>
            </van-cell>
            <van-cell title="检查方法：" class="detail-title" />
            <van-cell>
              <span style="color: #969799;">{{item.troubleshoot}}</span>
            </van-cell>
          </van-cell-group>
        </a-scroll>
        <div class="footer-action">
          <van-button class="action-item-main" color="#108EE9" @click="diagnosisShow = false">关闭</van-button>
        </div>
      </van-popup>
    </template>
  </page>
</template>

<script>
import Page from '../../components/Page'
import Detail from './Detail'
import navigate from '../../utils/navigate.js'
import io from '../../api'
import db from '../../db'
import native from '../../utils/native.js'
import AScroll from '../../components/Scroll'

export default {
  components: {
    Page,
    Detail,
    AScroll
  },
  data: () => ({
    headerHeightVW: 0,
    id: null,
    detail: {},
    online: false,
    user: JSON.parse(localStorage.getItem('user')),
    suggestionShow: false,
    diagnosisShow: false,
    diagnosisData: []
  }),
  methods: {
    onReady (header, main) {
      this.header = header

      this.headerHeightVW = (this.header.h + 1) * 100 / this.header.w

      this.id = navigate.getParam('id')

      this.loadDetail()

    },
    onShow () {
      this.loadDetail()
    },
    onShowSuggestion () {
      this.suggestionShow = true
    },
    onShowDiagnosis () {
      this.diagnosisShow = true
    },
    onClickLeft() {
      navigate.back()
    },
    handleRepair () {
      navigate.open('repair', {orderid: this.id})
    },
    handleSign () {
      navigate.open('photosign', {orderid: this.id})
    },
    loadDetail () {
      this.$toast.loading({
        duration: 0,
        loadingType: 'spinner'
      })

      db.getOrderDetail(this.id).then(data => {
        if (data.id == this.id) {
          this.online = false
          this.detail = {...data}

          this.diagnosisData = data.diagnosisData

          // alert(JSON.stringify(this.detail))
          
          // 数据同步
          // if (api.connectionType != 'none') {
          //   io.uploadOrder({...data}).catch(msg => {
          //     this.$toast('数据同步失败')
          //   })
          // }
          
          this.$toast.clear()
        } else {
          this.online = true
          io.getOrderDetail({id: this.id}).then(data => {
            this.detail = {...data}

            io.getOrderDiagnosis({id: this.id}).then(data => {
              this.diagnosisData = data

              // 如果是待维修状态或者待签到存储到本地
              if (this.detail.status == 'waiting_repair' || this.detail.status == 'pending_sign') {
                db.saveOrder({...this.detail, type: 1, diagnosisData: this.diagnosisData}).then(() => {
                  this.$toast.clear()
                })
              } else {
                this.$toast.clear()
              }
            })
          }).catch(msg => {
            this.$toast(msg)
          })
        }
      })
    },

    handleReferences () {
      // if (this.detail.device && this.detail.device.id) {
        navigate.open('references', {id: this.detail.device.id || ''})
      // }
    },

    handleReceive () {
      this.$toast.loading({
        duration: 0,
        forbidClick: true,
        loadingType: 'spinner',
        message: '正在接报...'
      })
      
      io.receiveOrder({
        id: this.detail.id
      }).then(() => {
        
        // 将该故障单存储到本地
        db.saveOrder({...this.detail, status: 'pending_sign', type: 1}).then(() => {
          this.$toast.clear()
          this.loadDetail()
        }).catch(msg => {
          this.$toast(msg)
        })
      }).catch(msg => {
        this.$toast(msg)
      })
      
    }, 

    handleForward () {

      if (native.connection()) {
        navigate.open('organization', {id: this.id})
      } else {
        alert('网络无法连接，转发请求失败')
      }
    },

    handleClearup () {
      if (api.connectionType == 'none') {
        alert('网络无法连接，销项请求失败')
      } else {
        navigate.open('clearup', {orderid: this.id})
      }
    },

    handleHang () {
      if (api.connectionType == 'none') {
        alert('网络无法连接，挂起请求失败')
      } else {
        navigate.open('hang', {orderid: this.id})
      }
    }
  }
}
</script>

<style lang="scss">
#header {
  .action-menu {
    width: 20px;
    vertical-align: middle;
    position: absolute;
    top: -35px;
    right: -5px;
  }
}
</style>
