<script>
import {statusEnum} from '../../utils/enum.js'
import AutoImage from '../../components/AutoImage'
import navigate from '../../utils/navigate.js'

export default {
  props: ['data'],
  components: {
    AutoImage
  },
  data: () => ({
    statusEnum: statusEnum
  }),
  methods: {
    handlePlayImages (index) {
      localStorage.setItem('photos', JSON.stringify(this.data.images))
      navigate.open('photoViewer', {index: index})
    },

    handleRepairSuggestion () {
      this.$emit('show-suggestion')
    },

    handleDiagnosis () {
      this.$emit('show-daiagnosis')
    },

    handlePlayVideo (item) {
      api.openVideo({
        url: item.url
        // url: 'http://7ynwhl.com1.z0.glb.clouddn.com/apicloud/66c0206cd4a74a9170c5deeb5a993955.mp4'
        // url: 'http://hs.jd.test.app.funenc.com/web/content/repair_manage.third_party_resource/137/file/1576345403694.mp4'
        // url: 'http://cdn.form.xiaoyaoji.cn/test.mp4'
        // url: 'http://docs.form.xiaoyaoji.cn/web/content/repair_manage.third_party_resource/137/file/1576345403694.mp4'
        // url: 'http://hs.jd.test.app.notcode.cn/web/content/repair_manage.third_party_resource/137/file/1576345403694.mp4'
        // url: 'http://172.16.109.44:8088/repairManageForXian/static/1576345403694.mp4'
      })
      // navigate.open('videoViewer', {url: 'http://hs.jd.test.app.notcode.cn/web/content/repair_manage.third_party_resource/137/file/1576345403694.mp4'})
    },

    handleRecord () {
      navigate.open('orderRecord', {id: this.data.id})
    }
  }
}
</script>

